/*TABLE HOMEPAGE*/

/* Floating div */
.floating-div {
  position: fixed;
  top: 20%;
  left: 20%;
  width: 60%;
  height: 70%;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

/* Header div */
.header-div {
  background-color: #8a3ab9; /* Dark purple */
  color: white;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

/* Filters */
.filters select {
  margin-right: 10px;
  padding: 5px;
  border-radius: 5px;
  border: none;
  color: #8a3ab9; /* Dark purple */
  /* Additional styles for filters */
}

/* Add button and view mode toggle styles */
.drk-btn {
  background-color:  #69249e ;
  font-family: 'Poppins', sans-serif;
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 25px;
  padding: 8px 12px;
  margin-right: 10px;
  cursor: pointer;
}
.drk-btn:hover {
  background-color: linear-gradient(to right, #af02ff, #FFCFAC) !important;
}
/* Toggle switch */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #8a3ab9; /* Dark purple */
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Body div */
.body-div {
  height: 80%;
  overflow-y: auto;
}

/* Table styles */
#data-table {
  width: 100%;
  border-collapse: collapse;
}

#data-table th,
#data-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

/* Table row actions (edit/delete icons) */
.action-icons {
  display: flex;
  justify-content: space-around;
}

.action-icons img {
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-right: 5px;
}

/* Highlighted row */
.highlight-row {
  background-color: #f0e8ff; /* Light purple */
}

/* tansaction page */
.filters {
